@keyframes lefttoright {
  0% {
    opacity: 0;
    transform: translateX(-130%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes righttoleft {
  0% {
    opacity: 0;
    transform: translateX(130%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.about__wrap {
  max-width: 70%;
  border: 2px solid var(--text-color);
  opacity: 0;
  border-radius: 10px;
