  margin-bottom: 30px;
  padding: 40px 30px;
  transition: scale .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.about__wrap:nth-child(2n+1) {
  animation: lefttoright .6s linear .5s forwards;
}

